import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import useMediaQuery from '@mui/material/useMediaQuery';
import { AppBar, Container, CssBaseline, Tab, Tabs, Toolbar, Typography } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';

import { FaSuitcaseRolling } from 'react-icons/fa';
import { ImBooks } from 'react-icons/im';
import { IoBarChartSharp, IoPaw } from 'react-icons/io5';

import Home from './Home';

function App() {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');

  const theme = React.useMemo(
    () =>
      createTheme({
        palette: {
          mode: prefersDarkMode ? 'dark' : 'light',
          primary: {
            main: '#c62828',
          },
          secondary: {
            main: '#1565c0',
            constractText: '#fff'
          }
        },
        typography: {
          fontFamily: '"Work Sans", "Helvetica", "Arial", sans-serif',
          fontSize: 12,
          fontWeightLight: 400,
          fontWeightRegular: 500,
          fontWeightMedium: 600,
          fontWeightBold: 800,
          subtitle1: {
            fontFamily: '"Lobster", cursive',
            fontSize: '1.2857142857142856rem'
          },
        },
        components: {
          MuiTab: {
            styleOverrides: {
              root: {
                color: '#fff'
              }
            }
          }
        }
      }),
    [prefersDarkMode]
  );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router basename="github-blog">
        <AppBar position='fixed'>
          <Toolbar>
            <Typography variant="subtitle1" component="div" sx={{flexGrow: 1}}>
              Dylan Boliske
            </Typography>
            <Tabs aria-label="nav tabs">
              <Tab icon={<ImBooks />} label="Course Work" iconPosition="start" />
              <Tab icon={<IoBarChartSharp />} label="Data Science" iconPosition="start" />
              <Tab icon={<FaSuitcaseRolling />} label="Travel" iconPosition="start" />
              <Tab icon={<IoPaw />} label="Cats" iconPosition="start" />
            </Tabs>
          </Toolbar>
        </AppBar>
        <Toolbar />
        <Container>
          <Switch>
            <Route path="/">
              <Home />
            </Route>
            <Route path="/course-work"></Route>
            <Route path="/data-science"></Route>
            <Route path="/travel"></Route>
            <Route path="/cats"></Route>
          </Switch>
        </Container>
      </Router>
    </ThemeProvider>
  );
}

export default App;
